import React, { Component } from 'react';

//import validator from 'validator';

export default class RadioCheck extends Component {
	onChangeHandler = (evt) => {
		this.props.data.onChangeHandler(evt);
	}
	render() {
		var data = this.props.data;
		var className = 'input-group';
		var checked = null;

		if(data.type === 'checkbox' || data.type === 'radio'){
			checked = data.checked;
		}

		if(data.selected === data.value){
			className += ' has-success';
		}

		return (
			<div className={className}>
				<span className="input-group-addon">
					<input type={data.type} name={data.name} className={data.inputClassName} id={data.id} placeholder={data.placeholder} onChange={this.onChangeHandler} value={data.value} checked={checked} />
				</span>
				<label htmlFor={data.id} className={data.labelClassName} dangerouslySetInnerHTML={data.labelText}></label>
			</div>
		);
	}
}

RadioCheck.propTypes = {
    data: React.PropTypes.object.isRequired
};



