import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import FormFields from './FormFields';
import ForeignHtml from './ForeignHtml';

import './PaymentMethods.css';


export default class PaymentMethods extends Component {
	constructor(props, context) {
		super(props, context);

		let current_saved = !!props.payment_methods.selected ? props.payment_methods.selected : null;

		let paymentMethodHTML = this._getSelectedHTML(props);

		let fields = props.payment_methods.all.map((field) => {
			field.onChangeHandler = this.onChangeHandler;
			field.selected = current_saved;
			return field;
		});

	    this.state = {
	    	current_saved: current_saved,
	    	continue_disabled: !!current_saved ? null : true,
	    	fields: fields,
	    	paymentMethodHTML: paymentMethodHTML
	    };
	}
	componentWillReceiveProps(nextProps) {
		let current_saved = !!nextProps.payment_methods.selected ? nextProps.payment_methods.selected : null;
		let fields = nextProps.payment_methods.all.map( (field) => {
			field.onChangeHandler = this.onChangeHandler;
			field.selected = current_saved;
			return field;
		});

		let paymentMethodHTML = this._getSelectedHTML(nextProps);

		this.setState({
			current_saved: current_saved,
			continue_disabled: !!current_saved ? null : true,
			fields: fields,
			paymentMethodHTML: paymentMethodHTML
		});
	}
	_getSelectedHTML = (props) => {
		let current_method = !!props.payment_methods.selected ? props.payment_methods.all.find((field) => {
			return field.value === props.payment_methods.selected;
		}) : '';

		let paymentMethodHTML = !!current_method ? current_method.displayOnSelect : '';
		return paymentMethodHTML;
	}
	onChangeHandler = (evt) => {
		var target = evt.target,
			val = target.value;

		let payment_methods = this.props.payment_methods;

		payment_methods.selected = val;

		this.props.onClickHandler(payment_methods, 'payment_methods');
	}
	updateChecked = (evt) => {
		var target = evt.target,
			val = target.value;

		var current_saved = this.state.current_saved,
			saved_addr = this.state.saved_addr;

		current_saved = saved_addr.find((addr) => {
			return val === addr.value;
		});

		this.setState({
			current_saved: current_saved
		});
	}
	_checkFormStatus = () => {
		let formWrapper = ReactDOM.findDOMNode(this.refs.paymentMethodsFormWrapper);
		let form = formWrapper.querySelector('#paymentMethodsForm');
		let errorFound = form.querySelector('.has-error');

		if(!!errorFound){
			this.setState({
				continue_disabled: true
			});
			return false;
		}
		else {
			this.setState({
				continue_disabled: null
			});
			return true;
		}
	}
	clickedContinue = (evt) => {
		evt.preventDefault();
		if(this._checkFormStatus()){
			this.props.onClickHandler(6, 'currentStep');
		}
	}
	render() {
		let paymentMethodHTML = !!this.state.paymentMethodHTML ? this.state.paymentMethodHTML : null;
		return (<article className="step-wrapper shipping-method-info">
			<section className="col-xs-12 shipping-method-form-wrap" ref="paymentMethodsFormWrapper">
				<h4>Select Your Payment Method</h4>
				<FormFields fields={this.state.fields} formId="paymentMethodsForm" />
				<ForeignHtml data={paymentMethodHTML} />
				<div className="button-row">
					<button type="button" className="btn btn-primary pull-right" onClick={this.clickedContinue} ref="continueBtn1" disabled={this.state.continue_disabled}>Continue</button>
				</div>
			</section>
		</article>);
	}
}

PaymentMethods.defaultProps = {
	onClickHandler: (evt) => {
    	evt.preventDefault();
    	return console.log(evt);
    }
}



