//import ajaxData from './ajaxData';

export default function getMagentoData() {
    var getDataFromHidden = function(inputId){
        var hiddenInput = document.querySelector(inputId);
        if(!hiddenInput){
            console.warn('Input '+inputId+' not found!');
            return null;
        }
        return hiddenInput.value;
    };

    var stringToJSON = function(stringData){
        if(!stringData){
            console.warn('stringData is type: '+ typeof(stringData));
            return null;
        }
        return JSON.parse(stringData);
    };

	return {
        magentoUrls:    stringToJSON(getDataFromHidden('#checkoutUrls')),
        userInfo:       stringToJSON(getDataFromHidden('#userInfo')),
        userAddresses:  stringToJSON(getDataFromHidden('#userAddresses')),
        cartInfo:           stringToJSON(getDataFromHidden('#cartInfo'))
    };
}


