import React, { Component } from 'react';
import './OrderConfirmation.css';

export default class OrderConfirmation extends Component {
	constructor(props, context) {
		super(props, context);

	    let initialState = {};
	    this.state = initialState;
	}
	render() {
		return (
		<div className="panel panel-default">
			<div className="panel-heading">
				<h3>Success!</h3>
			</div>
			<div className="panel-body">
				<article className="step-wrapper login-register">
					<section className="col-xs-12 col-sm-12 order-success center">
						<h1 className="section-title">Thanks for your order!</h1>
						<h2 className="section-subtitle">Your order number is {this.props.orderNumber}</h2>
						<p>Your order is now being processed. Please check your email for an order confirmtion email.</p>
						<p>We will send you email updates when your order has shipped.</p>
						<div className="button-row center">
							<button type="button" className="btn btn-primary">Shop some more</button>
						</div>
					</section>
				</article>
			</div>
		</div>);
	}
}



