import React from 'react';

export default function stepWrapper(Component, headingText, stepNumber) {
	return (props) => {
		var currentStep = props.currentStep ? Number(props.currentStep) : Number(props.data.currentStep);
		var editIconClassName = stepNumber < currentStep ? "glyphicon glyphicon-pencil" : "glyphicon glyphicon-pencil hidden";
		var panelColorClass = stepNumber > currentStep ? "panel panel-info" : "panel panel-success";
		var panelBodyClass = stepNumber === currentStep ? 'panel-body' : 'panel-body hidden';

		var returnToHandler = (evt) => {
			evt.preventDefault();
			props.onClickHandler(stepNumber, 'currentStep');
		};

		return (
			<div className={panelColorClass}>
  				<div className="panel-heading">
					<h3><span className="badge">{stepNumber}</span><span className="step-name">{headingText}</span> <i className={editIconClassName} onClick={returnToHandler}></i></h3>
				</div>
				<div className={panelBodyClass}>
					<Component {...props} />
				</div>
			</div>
		);
	}
}


