import React, { Component } from 'react';
import toCurrency from '../helpers/toCurrency';
import Table from './table/Table';

import './Review.css';

export default class Review extends Component {
	constructor(props, context) {
		super(props, context);

	    this.state = {
	    	shipping_price: 0,
	    	continue_disabled: false
	    };
	}
	componentDidUpdate(prevProps, prevState) {
		var tableLastCell = document.querySelector('#reviewTable tfoot td:last-child');

		if(!!tableLastCell && !!tableLastCell.scrollIntoView){
			tableLastCell.scrollIntoView();
		}
	}
	clickedContinue = (evt) => {
		evt.preventDefault();
		this.props.onClickHandler({
			currentStep: 7,
			orderNumber: '#10000123'
		});
	}
	render() {
		let props = this.props;
		let cart = props.cart;
		let shipping_method = !!props.shipping_methods.selected ? props.shipping_methods.all.find((method) =>{
			return method.value === props.shipping_methods.selected;
		}): {price: 0};

		let subtotal = !!props.subtotal ? props.subtotal : props.cart.subtotal;
		let grandtotal = !!props.grandtotal ? props.grandtotal : (props.cart.subtotal + shipping_method.price + props.taxes);

		//console.log(props.shipping_methods.selected);
		//console.log(shipping_method);

		let cartData = {
			thead: {
				rows: [
					{
						className: 'items-header',
						cellData: [
							{
								nodeName: 'th',
								text: 'Product Name'
							},
							{
								nodeName: 'th',
								text: 'SKU'
							},
							{
								nodeName: 'th',
								text: 'Price'
							},
							{
								nodeName: 'th',
								text: 'QTY'
							},
							{
								nodeName: 'th',
								text: 'Subtotal',
								className: 'text-right'
							}
						]
					}
				]
			},
			tfoot: {
				rows: [
					{
						className: 'subtotal-row',
						cellData: [
							{
							nodeName: 'td',
								text: 'Subtotal',
								colspan: '4',
								className: 'text-right'
							},
							{
								nodeName: 'td',
								text: toCurrency(subtotal),
								className: 'text-right'
							}
						]
					},
					{
						className: 'shipping-row',
						cellData: [
							{
								nodeName: 'td',
								text: 'Shipping',
								colspan: '4',
								className: 'text-right'
							},
							{
								nodeName: 'td',
								text: toCurrency(shipping_method.price),
								className: 'text-right'
							}
						]
					},
					{
						className: 'tax-row',
							cellData: [
							{
								nodeName: 'td',
								text: 'Taxes',
								colspan: '4',
								className: 'text-right'
							},
							{
								nodeName: 'td',
								text: toCurrency(props.taxes),
								className: 'text-right'
							}
						]
					},
					{
						className: 'total-row',
							cellData: [
							{
								nodeName: 'td',
								text: 'Grand Total',
								colspan: '4',
								className: 'text-right'
							},
							{
								nodeName: 'td',
								text: toCurrency(grandtotal),
								className: 'text-right'
							}
						]
					}
				]
			},
			rows: []
		};
		cartData.rows = cart.items.map((item, index) => {
			return {
				className: 'item-row',
				cellData: [
					{
						nodeName: 'td',
						text: item.name
					},
					{
						nodeName: 'td',
						text: item.sku
					},
					{
						nodeName: 'td',
						text: toCurrency(item.price)
					},
					{
						nodeName: 'td',
						text: item.qty
					},
					{
						nodeName: 'td',
						text: toCurrency(item.product_subtotal),
						className: 'text-right td-'+index
					}
				]
			}
		});

		return (<div>
			<Table tableId="reviewTable" {...cartData} />
			<div className="button-row">
				<button type="button" className="btn btn-primary pull-right" onClick={this.clickedContinue} ref="continueBtn1" disabled={this.state.continue_disabled}>Continue</button>
			</div>
		</div>);
	}
}

Review.defaultProps = {
	onClickHandler: (evt) => {
    	evt.preventDefault();
    	return console.log(evt);
    }
}



