import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import FormFields from './FormFields';
import SelectBox from './SelectBox';
import RadioCheck from './RadioCheck';

//import ajaxData from '../helpers/ajaxData';
import QueryableWorker from '../helpers/QueryableWorker';

import './Billing.css';

//var ajaxMethods = ajaxData();

export default class Billing extends Component {
	constructor(props, context) {
		super(props, context);

		let current_saved = !!props.addresses.billing ? props.addresses.billing : '';

		let options = !!props.addresses.all ? props.addresses.all : [];

		options.push({
			value: -1,
			label: 'New Address'
		});

	    this.state = {
	    	current_saved: current_saved,
	    	saved_addr: {
	    		name: 'shipping_address_id',
                id: 'shipping-address-select',
                inputClassName: 'input-text required-entry form-control',
    			labelClassName: 'required',
    			labelText: 'Saved Addresses',
    			placeholder: null,
	    		options: options,
	    		selected: current_saved,
	    		data_id: null
	    	},
	    	same_shipping: false,
	    	continue_disabled: !!current_saved ? null : true,
	    	fields: [
	    		{
	    			name: 			'billing[email]',
	    			id: 			'billing:email',
	    			type: 			'email',
	    			inputClassName: 'input-text required-entry form-control',
	    			labelClassName: 'required',
	    			labelText: 		'Email',
	    			placeholder: 	'Email Address'
	    		},
	    		{
	    			name: 			'billing[firstname]',
	    			id: 			'billing:firstname',
	    			type: 			'text', 
	    			inputClassName: 'input-text required-entry form-control',
	    			labelClassName: 'required',
	    			labelText: 		'First Name',
	    			placeholder: 	'First Name'
	    		},
	    		{
	    			name: 			'billing[lastname]',
	    			id: 			'billing:lastname',
	    			type: 			'text', 
	    			inputClassName: 'input-text required-entry form-control',
	    			labelClassName: 'required',
	    			labelText: 		'Last Name',
	    			placeholder: 	'Last Name'
	    		},
	    		{
	    			name: 			'billing[company]',
	    			id: 			'billing:company',
	    			type: 			'text', 
	    			inputClassName: 'input-text form-control',
	    			labelClassName: 'required',
	    			labelText: 		'Company',
	    			placeholder: 	'Company',
	    			optional: 		true
	    		},
	    		{
	    			name: 			'billing:street1',
	    			id: 			'billing:street1',
	    			type: 			'text',
	    			inputClassName: 'input-text required-entry form-control',
	    			labelClassName: 'required',
	    			labelText: 		'Address',
	    			placeholder: 	'Number and Street'
	    		},
	    		{
	    			name: 			'billing:street2',
	    			id: 			'billing:street2',
	    			type: 			'text',
	    			inputClassName: 'input-text form-control',
	    			labelClassName: 'required',
	    			labelText: 		'Address Line 2',
	    			placeholder: 	'Apartment Number',
	    			optional: 		true
	    		},
	    		{
	    			name: 			'billing[city]',
	    			id: 			'billing:city',
	    			type: 			'text',
	    			inputClassName: 'input-text required-entry form-control',
	    			labelClassName: 'required',
	    			labelText: 		'City',
	    			placeholder: 	'City'
	    		},
	    		{
	    			name: 			'billing[region_id]',
	    			id: 			'billing:region_id',
	    			type: 			'select',
	    			inputClassName: 'input-text required-entry form-control',
	    			labelClassName: 'required',
	    			labelText: 		'State/Province',
	    			placeholder: 	'Please select region, state or province',
	    			options: [
	    				{
	    					value: '43',
	    					label: 'New York'
	    				}
	    			],
	    			selected: 		''
	    		},
	    		{
	    			name: 			'billing[postcode]',
	    			id: 			'billing:postcode',
	    			type: 			'text',
	    			inputClassName: 'input-text required-entry form-control',
	    			labelClassName: 'required',
	    			labelText: 		'Postal Code',
	    			placeholder: 	'Postal Code'
	    		},
	    		{
	    			name: 			'billing[country_id]',
	    			id: 			'billing:country_id',
	    			type: 			'select',
	    			inputClassName: 'input-text required-entry form-control',
	    			labelClassName: 'required',
	    			labelText: 		'Country',
	    			placeholder: 	'Please select your country',
	    			options: [
	    				{
	    					value: 'US',
	    					label: 'United States'
	    				}
	    			],
	    			selected: 		''
	    		},
	    		{
	    			name: 			'billing[telephone]',
	    			id: 			'billing:telephone',
	    			type: 			'text',
	    			inputClassName: 'input-text form-control',
	    			labelClassName: 'required',
	    			labelText: 		'Phone Number',
	    			placeholder: 	'Phone Number',
	    			optional: 		true
	    		}
	    	],
	    	registerFields: [
	    		{
	    			name: 			'billing[customer_password]',
	    			id: 			'billing:customer_password',
	    			type: 			'password',
	    			inputClassName: 'input-text form-control',
	    			labelClassName: '',
	    			labelText: 		'Password',
	    			placeholder: 	'Password',
	    			optional: 		true
	    		},
	    		{
	    			name: 			'billing[confirm_password]',
	    			id: 			'billing:confirm_password',
	    			type: 			'password',
	    			inputClassName: 'input-text form-control',
	    			labelClassName: '',
	    			labelText: 		'Confirm',
	    			placeholder: 	'Confirm Password',
	    			optional: 		true
	    		}
	    	],
	    	showNewForm: (!!props.addresses.all && props.addresses.all.length <= 0) || !props.loggedin ? true : false,
	    	formData: {},
	    	message: ''
	    };
	}
	componentWillReceiveProps(nextProps) {
		let showNewForm = (!!nextProps.addresses.all && nextProps.addresses.all.length <= 0) || !nextProps.loggedin ? true : false;
		if(this.state.showNewForm !== showNewForm) {
			this.setState({
				showNewForm: showNewForm
			});
		}
	}
	updateChecked = (evt) => {
		var target = evt.target,
			val = Number(target.value);

		var current_saved = this.state.current_saved,
			saved_addr = this.state.saved_addr;

		current_saved = saved_addr.find((addr) => {
			return val === addr.value;
		});

		this.setState({
			current_saved: current_saved
		});
	}
	updateSameShipping = (evt) => {
		this.setState({
			same_shipping: evt.target.checked
		});
		var addresses = this.props.addresses;
		addresses.shipping = evt.target.value;

		this.props.onClickHandler(addresses, 'addresses');
	}
	_checkFormStatus = () => {
		if(!this.state.showNewForm && !!this.state.current_saved){
			return true;
		}
		let formWrapper = ReactDOM.findDOMNode(this.refs.billingFormWrapper);
		let form = formWrapper.querySelector('#billingForm');
		let continueBtn1 = ReactDOM.findDOMNode(this.refs.continueBtn1);
		let continueBtn2 = ReactDOM.findDOMNode(this.refs.continueBtn2);
		let errorFound = form.querySelector('.has-error');
		if(!!errorFound){
			if(!!continueBtn1){
				continueBtn1.setAttribute('disabled', 'disabled');
			}
			if(!!continueBtn2){
				continueBtn2.setAttribute('disabled', 'disabled');
			}

			return false;
		}
		else {
			if(!!continueBtn1){
				continueBtn1.removeAttribute('disabled');
			}
			if(!!continueBtn2){
				continueBtn2.removeAttribute('disabled');
			}
			return true;
		}
	}
	toggleForm = (evt, target) => {
		let $target =  target || evt.target;
		let value = $target.value;

		let saved_addr = this.state.saved_addr;
		saved_addr.selected = value;

		let addr = saved_addr.options.find( (addr, index) => {
			return addr.value === value;
		});

		saved_addr.data_id = !!addr.data_id ? addr.data_id : null;

		//console.log(saved_addr.data_id);

		this.setState({
			showNewForm: ~~value === -1,
			saved_addr: saved_addr
		});
	}
	_updateFormData = (evt, target) => {
		let $target = target || evt.target;
		let name = $target.getAttribute('name');
		let value = $target.value;

		let state = this.state;
		state.formData[name] = $target.value;

		if($target.nodeName.toLowerCase() === 'select'){
			state.fields.forEach((field) => {
				if(field.name === name){
					field.selected = value;
				}
			});
		}

		this.setState(state);
	}
	clickedContinue = (evt) => {
		evt.preventDefault();
		let nextStep = 3;
		if( this._checkFormStatus() ){
			if(!!this.state.same_shipping){
				nextStep = 4;
			}

			if( this.props.workerUrl.indexOf('localhost:3000') <= -1 ){
				let formData = {};

				if(!!this.props.loggedin && !this.state.showNewForm && !!this.state.saved_addr){
					formData['billing_address_id'] = this.state.saved_addr.selected;
					formData['billing[address_id]'] = this.state.saved_addr.data_id;

					if(this.state.same_shipping){
						formData['billing[use_for_shipping]'] = 1;
					}
				}
				else {
					formData = Object.assign({}, this.state.formData);
					formData['billing[street][]'] = [formData['billing:street1'], formData['billing:street2']].join("\n");
				
					delete formData['billing:street1'];
					delete formData['billing:street2'];
				}

				let _errorCheck = (msg) => {
					let json = JSON.parse(msg);
					if(json.error){
						return this.setState({
							message: json.message
						});
					}

					let shipping_methods = this.props.shipping_methods;
					if(!!json.update_section && !!json.update_section.methods){
						shipping_methods.all = json.update_section.methods.map( (method) => {
							return Object.assign({}, shipping_methods.defaultStyles, method);
						});
					}	

					this.props.onClickHandler({
						shipping_methods: shipping_methods,
						currentStep: nextStep
					});
				};

				let myWorker = new QueryableWorker(this.props.workerUrl, _errorCheck, _errorCheck);
				myWorker.sendQuery('set', {url: this.props.save.billing, body: formData});
			}

			if( this.props.workerUrl.indexOf('localhost:3000') > -1 ){
				//console.log(this.state.saved_addr);
				this.props.onClickHandler({
					currentStep: nextStep
				});
			}
		}
	}
	
	render() {

		let sameForShipping = {
			id: 				'billing:use_for_shipping_yes',
			name: 				'billing[use_for_shipping]',
			value: 				'1',
			labelText: 			{__html: '<span>Same for Shipping</span>'},
			labelClassName: 	'form-control',
			type: 				'checkbox',
			onChangeHandler: 	this.updateSameShipping,
			checked: 			this.state.same_shipping,
			optional: 			true
		};

		let savedAddrHTML = null;
		let newAddrHTML = null;
		let msgHTML = null;

		if(!!this.state.message){
			msgHTML = <section className="col-xs-12" ref="messageWrapper"><p>{this.state.message}</p></section>;
		}

		let savedAddrClassName = "col-xs-12 billing-saved";
		let newAddrClassName = "col-xs-12 billing-form-wrap";

		if(!this.props.loggedin || this.props.addresses.all.length <= 0){
			savedAddrClassName += ' hidden';
		}

		if(!this.state.showNewForm){
			newAddrClassName += ' hidden';
		}

		let newAddrFields = this.state.fields;
		if(!!this.props.register){
			newAddrFields = newAddrFields.concat(this.state.registerFields);
		}

		let saved_addr = this.state.saved_addr;

		savedAddrHTML = !this.props.loggedin ? '' : <section className={savedAddrClassName}>
			<SelectBox {...saved_addr} onUpdateValue={this.toggleForm} />
			<div className="button-row">
				<RadioCheck data={sameForShipping} />
				<button type="button" className="btn btn-primary pull-right" onClick={this.clickedContinue} ref="continueBtn1">Continue</button>
			</div>
		</section>;
		
		newAddrHTML = <section className={newAddrClassName} ref="billingFormWrapper">
			<h4>Add a New Address</h4>
			<FormFields fields={newAddrFields} formId="billingForm" onInputChange={this._updateFormData} onUpdateValue={this._updateFormData} />
			<div className="button-row">
				<button type="button" className="btn btn-primary pull-right" onClick={this.clickedContinue} ref="continueBtn2">Save &amp; Continue</button>
			</div>
		</section>;

		return (<article className="step-wrapper billing-info">
			{msgHTML}
			{savedAddrHTML}
			{newAddrHTML}
		</article>);
	}
}

Billing.defaultProps = {
	onClickHandler: (evt) => {
    	evt.preventDefault();
    	return console.log(evt);
    }
}



