
export default function QueryableWorker(sURL, fDefListener, fOnError) {
	// if(!window.Worker){
	// 	console.log('This browser does not support Webworkers');
	// 	return;
	// }

    var oInstance = this,
        oWorker = new Worker(sURL),
        oListeners = {};

    this.defaultListener = fDefListener || function(oEvent) {
    	console.table(arguments);
    };

    oWorker.onmessage = function(oEvent) {
        if (oEvent.data instanceof Object && oEvent.data.hasOwnProperty("vo42t30") && oEvent.data.hasOwnProperty("rnb93qh")) {
            oListeners[oEvent.data.vo42t30].apply(oInstance, oEvent.data.rnb93qh);
        } else {
            this.defaultListener.call(oInstance, oEvent.data);
        }
    }.bind(this);

    if (fOnError) {
    	oWorker.onerror = fOnError;
    }

    this.sendQuery = function(type, xhrdata) {
        if (arguments.length < 1) {
            throw new TypeError("QueryableWorker.sendQuery - not enough arguments");
        }
        oWorker.postMessage({ "type": type, "xhrdata": xhrdata });
    };

    this.postMessage = function(vMsg) {
        oWorker.postMessage(vMsg);
    };

    this.terminate = function() {
        oWorker.terminate();
    };

    this.addListener = function(sName, fListener) {
        oListeners[sName] = fListener;
    };

    this.removeListener = function(sName) {
        delete oListeners[sName];
    };
}



