import React, { Component } from 'react';
import FormField from './FormField';

export default class FormFields extends Component {
	render() {
		let fields = this.props.fields.map( (field, index) => {
			return (<li className="list-group-item form-item" key={index}>
				<FormField key={index} data={field} onInputChange={this.props.onInputChange} onUpdateValue={this.props.onUpdateValue} />
			</li>);
		});

		return (
			<form id={this.props.formId} ref={this.props.formId} className="form-horizontal" >
				<ul className="list-group form-list">
					{fields}
				</ul>
			</form>
		);
	}
}


