import React, { Component } from 'react';
import SelectBox from './SelectBox';
import RadioCheck from './RadioCheck';

import validator from 'validator';

export default class FormField extends Component {
	constructor(props, context) {
		super(props, context);

		this.state = {
			valid: null,
			value: props.value || ''
		}
	}
	validateField = (evt) => {
		var isValid = true;

		var input = evt.target,
			val = input.value.trim();

		if(!!this.props.data.optional){
			this.setState({
				value: val,
				valid: isValid
			});

			this.props.onInputChange(evt);
		}

		if(!val){
			isValid = false;
		}
		//other validations by data.type

		if(input.type === 'email' && !validator.isEmail(val) ){
			isValid = false;
		}
		
		this.setState({
			value: val,
			valid: isValid
		});
		
		if(!!isValid){
			this.props.onInputChange(evt);
		}
	}
	render() {
		var data = this.props.data ? this.props.data : this.props;
		var className = 'input-group';

		if(data.type === 'select'){
			return (
				<SelectBox {...data} onUpdateValue={this.props.onUpdateValue} />
			);
		}
		if(data.type === 'radio' || data.type === 'checkbox'){
			return (
				<RadioCheck data={data} onChangeHandler={this.props.onInputChange} />
			);
		}

		if(this.state.valid === true){
			className += ' has-success';
		}
		if(this.state.valid === false){
			className += ' has-error';
		}

		return (
			<div className={className}>
				<span className="input-group-addon">
					<label htmlFor={data.id} className={data.labelClassName}>{data.labelText}</label>
				</span>
				<input type={data.type} className={data.inputClassName} id={data.id} name={data.name} placeholder={data.placeholder} onChange={this.validateField} onBlur={this.validateField} value={this.state.value} />
			</div>
		);
	}
}

FormField.propTypes = {
    data: React.PropTypes.object.isRequired
};

FormField.defaultProps = {
	onInputChange: (evt) => {
    	evt.preventDefault();
    }
}


