import React, { Component } from 'react';

export default class ForeignHtml extends Component {
	render() {
		var toDisplay = this.props.data;
		var className = this.props.className ? this.props.className : 'foreign-html-wrapper';

		return (
			<div className={className}>
				<div dangerouslySetInnerHTML={toDisplay}></div>
			</div>
		);
	}
}

