import React, { Component } from 'react';

export default class TableCell extends Component {
	render() {
		let props = this.props;

		if(!props){
			return null;
		}

		let textData = props.text;

		let className = 'table-cell';
		if(!!props.className){
			className = className+' '+props.className;
		}

		let colspan = null;
		if(!!props.colspan){
			colspan = props.colspan;
		}

		let cell = null;
		if(props.nodeName === 'th'){
			cell = <th className={className} colSpan={colspan}>{textData}</th>
		}
		else {
			cell = <td className={className} colSpan={colspan}>{textData}</td>
		}

		return cell;
	}
}


