
export default function toCurrency(num, symbol, local) {
	symbol = !!symbol ? symbol : '$';
	local = !!local ? local : 'en-US';
	if(!!num.toLocaleString){
		return symbol+ num.toLocaleString(local, {minimumFractionDigits: 2});
	}
	return symbol+ num.toFixed(2);
}


