import React, { Component } from 'react';
import FormFields from './FormFields';

import validator from 'validator';

import './ForgotPassword.css';

export default class ForgotPassword extends Component {
	constructor(props, context) {
		super(props, context);

	    let initialState = {
	    	fields: [
	    		{
	    			name: 			'email',
	    			id: 			'email_address',
	    			type: 			'email', 
	    			inputClassName: 'input-text required-entry validate-email form-control',
	    			labelClassName: 'required',
	    			labelText: 		'Email Address',
	    			placeholder: 	'example@gmail.com'
	    		}
	    	],
	    	values: {
	    		email: ''
	    	},
	    	showErrorMessage: false,
	    	showSuccessMessage: false,
	    	messages: {
	    		success: 'A password reset email has been sent to %email%, please click the link there.',
	    		error: 'We are unable to send you a password reset email at this time, please try again later or contact customer support.'
	    	}
	    };

	    this.state = initialState;
	}
	onInputChange = (evt) => {
		var $target = evt.target;
		var name = $target.getAttribute('type');

		var state = this.state;
		state.values[name] = $target.value;

		this.setState(state);
	}
	updateChecked = (evt) => {
		var target = evt.target,
			val = target.value;

		var checkout_methods = this.state.checkout_methods;

		checkout_methods.map((method) => {
			if(val === method.value){
				method.checked = true;
			}
			else {
				method.checked = false;
			}
			return method;
		});

		this.setState({
			checkout_methods: checkout_methods
		});
	}
	clickedSubmit = (evt) => {
		evt.preventDefault();

		let email_address = this.state.values.email;
		if(!email_address || !validator.isEmail(email_address)){
			let $email_address = document.getElementById('email_address');
			if(!!$email_address && !!$email_address.focus){
				$email_address.focus();
			}
			return;
		}

		//assume success
		this.setState({
			showSuccessMessage: true
		});
	}
	render() {
		let backToLogin = <button type="button" className="btn btn-sm btn-info pull-left" onClick={this.props.fpToggle}>&laquo; Back to Login</button>;
		
		if(!!this.state.showErrorMessage){
			return (
				<article className="step-wrapper login-register">
					<section className="col-xs-12 col-sm-12 forgot-password">
						<div className="alert alert-danger">
							<h5>{this.state.messages.error}</h5>
						</div>
						{backToLogin}
					</section>
				</article>
			);
		}

		if(!!this.state.showSuccessMessage){
			return (
				<article className="step-wrapper login-register">
					<section className="col-xs-12 col-sm-12 forgot-password">
						<div className="alert alert-success">
							<h5>{this.state.messages.success.replace('%email%', this.state.values.email)}</h5>
						</div>
						{backToLogin}
					</section>
				</article>
			);
		}

		return (<article className="step-wrapper login-register">
			<section className="col-xs-12 col-sm-12 forgot-password">
				<h1 className="section-title">Forgot Your Password?</h1>
				<h2 className="section-subtitle">Please enter your email address below. You will receive a link to reset your password.</h2>
				<FormFields formId="forgotPassword" fields={this.state.fields} onInputChange={this.onInputChange} />
				<div className="button-row">
					{backToLogin}
					<button type="button" className="btn btn-primary pull-right" onClick={this.clickedSubmit} >Send Email</button>
				</div>
			</section>
		</article>);
	}
}

ForgotPassword.defaultProps = {
	fpToggle: (evt) => {
    	evt.preventDefault();
    	return console.log(evt);
    }
}



