import React, { Component } from 'react';

export default class SelectBox extends Component {
	constructor(props, context) {
		super(props, context);

		this.state = {
			valid: '',
			value: props.value || props.selected || ''
		}
	}
	validateField = (evt) => {
		var isValid = true;

		var input = evt.target,
			val = input.value.trim();

		if(!!this.props.optional){
			return this.setState({
				value: val,
				valid: ''
			}, () => {
				if(!!this.props.onUpdateValue){
					this.props.onUpdateValue(evt, input);
				}
			});
		}

		if(val === null || val === undefined){
			isValid = false;
		}

		return this.setState({
			value: val,
			valid: isValid
		}, () => {
			if(!!this.props.onUpdateValue){
				this.props.onUpdateValue(evt, input);
			}
		});
	}
	componentWillReceiveProps(nextProps) {
		let valid = nextProps.selected === undefined ? false : true;
		valid = nextProps.selected === null ? '' : valid;

		this.setState({
			valid: valid,
			value: nextProps.selected
		});
	}
	render() {
		var data = this.props;
		var options = data.options;

		var className = 'input-group';

		if(this.state.valid === null || this.state.valid === ''){
			//do nothing to the className
		}
		else if(this.state.valid === true){
			className += ' has-success';
		}
		else if(this.state.valid === false){
			className += ' has-error';
		}

		if(data.placeholder !== null && (options[0].label !== data.placeholder)){
			options.unshift({value: null, label: data.placeholder});
		}

		var optionsHTML = options.map( (option, index) => {
			return <option key={index} value={option.value} data-id={option.data_id} >{option.label}</option>
		});

		return (
			<div className={className}>
				<span className="input-group-addon">
					<label htmlFor={data.id} className={data.labelClassName}>{data.labelText}</label>
				</span>
				<select className={data.inputClassName} id={data.id} name={data.name} value={this.state.value} onChange={this.validateField} onBlur={this.validateField}>
					{optionsHTML}
				</select>
				<i className="glyphicon glyphicon-option-vertical"></i>
			</div>
		);
	}
}

