import React, { Component } from 'react';
//import ReactDOM from 'react-dom';
import FormFields from './FormFields';
import ForgotPassword from './ForgotPassword';

import QueryableWorker from '../helpers/QueryableWorker';

import './LoginRegister.css';

export default class LoginRegister extends Component {
	constructor(props, context) {
		super(props, context);

	    this.state = {
	    	checkout_index: 0,
	    	checkout_methods: [
	    		{
	    			value: 'guest',
	    			label: 'Checkout as Guest',
	    			checked: true
	    		},
	    		{
	    			value: 'register',
	    			label: 'Create an Account',
	    			checked: false
	    		}
	    	],
	    	fields: [
	    		{
	    			name: 			'login[username]',
	    			id: 			'login-email',
	    			type: 			'email', 
	    			inputClassName: 'input-text required-entry validate-email form-control',
	    			labelClassName: 'required',
	    			labelText: 		'Email Address',
	    			placeholder: 	'Email'
	    		},
	    		{
	    			name: 			'login[password]',
	    			id: 			'login-password',
	    			type: 			'password', 
	    			inputClassName: 'input-text required-entry validate-password form-control',
	    			labelClassName: 'required',
	    			labelText: 		'Password',
	    			placeholder: 	'Password'
	    		}
	    	],
	    	values: {
	    		email: '',
	    		password: ''
	    	},
	    	showFP: false
	    };
	}
	onInputChange = (evt) => {
		var $target = evt.target;
		var name = $target.getAttribute('type');

		var state = this.state;
		state.values[name] = $target.value;

		this.setState(state);
	}
	updateChecked = (evt) => {
		var target = evt.target,
			val = target.value;

		var checkout_methods = this.state.checkout_methods;
		var selected_method = 0;

		checkout_methods.map((method, index) => {
			if(val === method.value){
				method.checked = true;
				selected_method = index;
			}
			else {
				method.checked = false;
			}
			return method;
		});

		this.setState({
			checkout_methods: checkout_methods,
			checkout_index: selected_method
		});
	}
	clickedContinue = (evt) => {
		evt.preventDefault();

		if( this.props.workerUrl.indexOf('localhost:3000') <= -1 ){
			let _errorCheck = (msg) => {
				let json = JSON.parse(msg);
				if(json.error){
					return this.setState({
						message: json.message
					});
				}
				this.props.onClickHandler({
					currentStep: 2,
					register: !!this.state.checkout_index
				});
			};

			let formData = {
				method: this.state.checkout_methods[this.state.checkout_index].value
			};

			let myWorker = new QueryableWorker(this.props.workerUrl, _errorCheck, _errorCheck);
			myWorker.sendQuery('set', {url: this.props.save.method, body: formData});
		}
		else {
			this.props.onClickHandler({
				currentStep: 2,
				register: !!this.state.checkout_index
			});
		}
		
	}
	clickedLogin = (evt) => {
		evt.preventDefault();
		if( this.props.workerUrl.indexOf('localhost:3000') <= -1 ){
			let _errorCheck = (msg) => {
				let json = JSON.parse(msg);
				if(json.error){
					return this.setState({
						message: json.message
					});
				}
				this.props.onClickHandler({
					currentStep: 2,
					loggedin: true,
					username: this.state.values.email
				});
			};

			let formData = {
				form_key: this.props.form_key,
				'login[username]': this.state.values.email,
				'login[password]': this.state.values.password,
			};

			let myWorker = new QueryableWorker(this.props.workerUrl, _errorCheck, _errorCheck);
			myWorker.sendQuery('set', {url: this.props.save.login, body: formData});
		}
		else {
			this.props.onClickHandler({
				currentStep: 2,
				loggedin: true,
				username: this.state.values.email
			});
		}
	}
	clickedFP = (evt) => {
		evt.preventDefault();

		this.setState({
			showFP: !this.state.showFP
		});
	}
	render() {
		let namePrefix = 'checkout_method';
		let listItems = this.state.checkout_methods.map((method, index)=> {
			return <li className="list-group-item radio-option-item" key={index}>
						<div className="input-group">
							<span className="input-group-addon">
								<input type="radio" id={namePrefix+method.value} name="checkout_method" defaultValue={method.value} onChange={this.updateChecked} checked={method.checked} />
							</span>
							<label htmlFor={namePrefix+method.value} className="form-control">{method.label}</label>
						</div>
					</li>;
		});

		if(!!this.state.showFP){
			return (
				<ForgotPassword fpToggle={this.clickedFP} />
			);
		}

		return (<article className="step-wrapper login-register">
			<section className="col-xs-12 col-sm-6 guest-register">
				<h1 className="section-title">Checkout as a Guest or Register</h1>
				<h2 className="section-subtitle">Register with us future convenience:</h2>
				<ul className="list-group radio-options-list">
					{listItems}
				</ul>
				<div className="button-row">
					<button type="button" className="btn btn-primary pull-right" onClick={this.clickedContinue} >Continue</button>
				</div>
			</section>
			<section className="col-xs-12 col-sm-6 user-login">
				<h1 className="section-title">Login</h1>
				<h2 className="section-subtitle">Please login below:</h2>
				<FormFields formId="loginForm" fields={this.state.fields} onInputChange={this.onInputChange} />
				<div className="button-row">
					<button type="button" className="btn btn-sm btn-info pull-left" onClick={this.clickedFP}>Forgot Password</button>
					<button type="button" className="btn btn-primary pull-right" onClick={this.clickedLogin} >Login</button>
				</div>
			</section>
		</article>);
	}
}

LoginRegister.defaultProps = {
	onClickHandler: (evt) => {
    	evt.preventDefault();
    	return console.log(evt);
    }
}



