import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import FormFields from './FormFields';

import QueryableWorker from '../helpers/QueryableWorker';

import './ShippingMethods.css';


export default class ShippingMethods extends Component {
	constructor(props, context) {
		super(props, context);

		let current_saved = !!props.shipping_methods.selected ? props.shipping_methods.selected : null;

		let fields = props.shipping_methods.all.map((method) => {
			let field = Object.assign({}, props.shipping_methods.defaultStyles, method, {onChangeHandler: this.onChangeHandler, selected: current_saved});
			return field;
		});

	    this.state = {
	    	current_saved: current_saved,
	    	continue_disabled: !!current_saved ? null : true,
	    	fields: fields,
	    	message: null
	    };
	}
	componentWillReceiveProps(nextProps) {
		let current_saved = !!nextProps.shipping_methods.selected ? nextProps.shipping_methods.selected : null;
		
		let fields = nextProps.shipping_methods.all.map( (method) => {
			let field = Object.assign({}, nextProps.shipping_methods.defaultStyles, method, {onChangeHandler: this.onChangeHandler, selected: current_saved});
			return field;
		});

		this.setState({
			current_saved: current_saved,
			continue_disabled: !!current_saved ? null : true,
			fields: fields
		}); 
	}
	onChangeHandler = (evt) => {
		var target = evt.target,
			val = target.value;

		let shipping_methods = this.props.shipping_methods;

		shipping_methods.selected = val;

		this.props.onClickHandler(shipping_methods, 'shipping_methods');
	}
	updateChecked = (evt) => {
		var target = evt.target,
			val = target.value;

		var current_saved = this.state.current_saved,
			saved_addr = this.state.saved_addr;

		current_saved = saved_addr.find((addr) => {
			return val === addr.value;
		});

		this.setState({
			current_saved: current_saved
		});
	}
	_checkFormStatus = () => {
		let formWrapper = ReactDOM.findDOMNode(this.refs.shippingMethodFormWrapper);
		let form = formWrapper.querySelector('#shippingMethodForm');
		let errorFound = form.querySelector('.has-error');

		if(!!errorFound){
			this.setState({
				continue_disabled: true
			});
			return false;
		}
		else {
			this.setState({
				continue_disabled: null
			});
			return true;
		}
	}
	clickedContinue = (evt) => {
		evt.preventDefault();
		if(this._checkFormStatus()){
			let nextStep = 5;

			let formData = {
				shipping_method: this.props.shipping_methods.selected
			};

			let _errorCheck = (msg) => {
				let json = JSON.parse(msg);
				if(json.error){
					return this.setState({
						message: json.message
					});
				}

				let payment_methods = this.props.payment_methods;
				if(!!json.update_section && !!json.update_section.methods){
					payment_methods.all = json.update_section.methods.map( (method) => {
						return Object.assign({}, payment_methods.defaultStyles, method);
					});
				}

				this.props.onClickHandler({
					shipping_price:  this.props.shipping_methods.selected.price,
					payment_methods: payment_methods,
					currentStep: nextStep
				});
			};


			//_errorCheck('{"goto_section":"payment","update_section":{"name":"payment-method","html":""}}');

			let myWorker = new QueryableWorker(this.props.workerUrl, _errorCheck, _errorCheck);
			myWorker.sendQuery('set', {url: this.props.save.shippingMethod, body: formData});
		}
	}
	render() {

		return (<article className="step-wrapper shipping-method-info">
			<section className="col-xs-12 shipping-method-form-wrap" ref="shippingMethodFormWrapper">
				<h4>Select Your Shipping Method</h4>
				<FormFields fields={this.state.fields} formId="shippingMethodForm" />
				<div className="button-row">
					<button type="button" className="btn btn-primary pull-right" onClick={this.clickedContinue} ref="continueBtn1" disabled={this.state.continue_disabled}>Continue</button>
				</div>
			</section>
		</article>);
	}
}

ShippingMethods.defaultProps = {
	onClickHandler: (evt) => {
    	evt.preventDefault();
    	return console.log(evt);
    }
}



