import React, { Component } from 'react';

import stepWrapper from './components/stepWrapper';
import LoginRegister from './components/LoginRegister';
import Billing from './components/Billing';
import Shipping from './components/Shipping';
import ShippingMethods from './components/ShippingMethods';
import PaymentMethods from './components/PaymentMethods';
import Review from './components/Review';
import OrderConfirmation from './components/OrderConfirmation';

//import arrayToHTML from './helpers/arrayToHTML';

/**
 * REMOVE BEFORE CONNECTING TO MAGENTO
 */
import getTestData from './helpers/getTestData';
/**
 * END REMOVE
 */
/**
 * MAGENTO Connector
 */
import getMagentoData from './helpers/getMagentoData';
/**
 * END
 */

//import logo from './logo.svg';
import './App.css';

var LoginStep   = stepWrapper(LoginRegister, 'Checkout Method', 1);
var BillingStep = stepWrapper(Billing, 'Billing Information', 2);
var ShippingStep = stepWrapper(Shipping, 'Shipping Information', 3);
var ShippingMethodsStep = stepWrapper(ShippingMethods, 'Shipping Method', 4);
var PaymentMethodsStep = stepWrapper(PaymentMethods, 'Payment Method', 5);
var ReviewStep = stepWrapper(Review, 'Review Order', 6);

export default class App extends Component {
    constructor(props, context) {
        super(props, context);

        var defaultState = {
            form_key: null,

            baseUrl:    '/',
            workerUrl:  'http://localhost:3000/src/helpers/ajaxWorker.js',
            progress:   'checkout/onepage/progress',
            cartUrl:    'checkout/cart',

            save: {
                'method'        : 'checkout/onepage/saveMethod', //login step - customer type
                'login'         : 'customer/account/loginPost',
                'billing'       : 'checkout/onepage/saveBilling',
                'shipping'      : 'checkout/onepage/saveShipping',
                'shippingMethod': 'checkout/onepage/saveShippingMethod',
                'giftOptions'   : 'checkout/onepage/getAdditional',
                'payment'       : 'checkout/onepage/savePayment',
                'review'        : 'checkout/onepage/review',
                'order'         : 'checkout/onepage/saveOrder/'
            },
            loggedin: false,
            username: 'Guest',
            register: false,
            currentStep: 1,
            addresses: {
                all: [],
                billing: '',
                shipping: ''
            },
            shipping_methods: {
                all: [],
                defaultStyles: {
                    name:            'shipping_method',
                    id:              's_method_default_fallback',
                    type:            'radio',
                    value:           'default_fallback',
                    price:           0.00,
                    inputClassName:  'input-text required-entry',
                    labelClassName:  'required form-control',
                    labelText:       'Standard Shipping',
                    placeholder:     null,
                    selected:        ''
                },
                selected: '',
                is_gift: false,
                gift_msg: '',
                delivery_msg: ''
            },
            payment_methods: {
                all: [],
                defaultStyles: {
                    name:            'payment_method',
                    id:              'p_method_default_fallback',
                    type:            'radio',
                    value:           'default_fallback',
                    inputClassName:  'input-text required-entry',
                    labelClassName:  'required form-control',
                    labelText:       'Example Payment Method',
                    placeholder:     null,
                    selected:        ''
                },
                selected: ''
            },
            cart: {
                items: [],
                subtotal: 0.00,
                taxes: 0.00,
                grandtotal: 0.00
            },
            shipping_price: 0.00,
            taxes: 0.00,
            subtotal: 0.00,
            grandtotal: 0.00,
            orderNumber: null
        };

        /**
         * Update the default state object here:
         * REMEMEBER TO REMOVE THE TEST DATA!!!
         */
        var startingState = Object.assign({}, defaultState, {form_key: this._getFormKey()}, getTestData());

        var magentoData = getMagentoData();
        if(magentoData.magentoUrls){
            startingState = Object.assign(startingState, magentoData.magentoUrls, magentoData.cartInfo, magentoData.userInfo, { addresses: magentoData.userAddresses });
        }

        if(!startingState.addresses.billing && !!startingState.addresses.all.length){
            startingState.addresses.billing = startingState.addresses.all[0].value;
        }
        if(!startingState.addresses.shipping && !!startingState.addresses.all.length){
            startingState.addresses.shipping = startingState.addresses.all[0].value;
        }

        this.state = startingState;
    }
    _getFormKey = () => {
        var hiddenInput = document.querySelector('#form_key');
        return !!hiddenInput ? hiddenInput.value : null;
    }
    _getUpdatedState = (data, propToUpdate) => {
        if(!data){
            return console.warn('No state data sent');
        }

        var state = this.state;
        if(!!propToUpdate && !!state[propToUpdate]){
            state[propToUpdate] = data;
        }
        else {
            state = Object.assign({}, this.state, data);
        }

        this.setState(state);
    }
    render() {
        var state = this.state;
        var loginStep = !!state.loggedin ? '': <LoginStep {...state} onClickHandler={this._getUpdatedState} />;
        
        if(this.state.orderNumber !== null){
            return (
                <div className="App">
                    <div className="container">
                        <OrderConfirmation {...state} />
                    </div>
                </div>
            );
        }

        return ( 
            <div className="App">
                <div className="container">
                    {loginStep}
                    <BillingStep {...state} onClickHandler={this._getUpdatedState} />
                    <ShippingStep {...state} onClickHandler={this._getUpdatedState} />
                    <ShippingMethodsStep {...state} onClickHandler={this._getUpdatedState} />
                    <PaymentMethodsStep {...state} onClickHandler={this._getUpdatedState} />
                    <ReviewStep {...state} onClickHandler={this._getUpdatedState} />
                </div>
            </div>
        );
    }
}



