import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import FormFields from './FormFields';
import SelectBox from './SelectBox';

import QueryableWorker from '../helpers/QueryableWorker';

import './Shipping.css';

export default class Shipping extends Component {
	constructor(props, context) {
		super(props, context);

		let current_saved = !!props.addresses.shipping ? props.addresses.shipping : '';

		let options = !!props.addresses.all ? props.addresses.all : [];

	    this.state = {
	    	current_saved: current_saved,
	    	saved_addr: {
	    		name: 			'shipping_address_id',
                id: 			'shipping-address-select',
                inputClassName: 'input-text required-entry form-control',
    			labelClassName: 'required',
    			labelText: 		'Saved Addresses',
    			placeholder: 	null,
	    		options: 		options,
	    		selected: 		current_saved
	    	},
	    	continue_disabled: 	!!current_saved ? null : true,
	    	fields: [
	    		{
	    			name: 			'shipping[firstname]',
	    			id: 			'shipping:firstname',
	    			type: 			'text', 
	    			inputClassName: 'input-text required-entry form-control',
	    			labelClassName: 'required',
	    			labelText: 		'First Name',
	    			placeholder: 	'First Name'
	    		},
	    		{
	    			name: 			'shipping[lastname]',
	    			id: 			'shipping:lastname',
	    			type: 			'text', 
	    			inputClassName: 'input-text required-entry form-control',
	    			labelClassName: 'required',
	    			labelText: 		'Last Name',
	    			placeholder: 	'Last Name'
	    		},
	    		{
	    			name: 			'shipping[company]',
	    			id: 			'shipping:company',
	    			type: 			'text', 
	    			inputClassName: 'input-text form-control',
	    			labelClassName: 'required',
	    			labelText: 		'Company',
	    			placeholder: 	'Company',
	    			optional: 		true
	    		},
	    		{
	    			name: 			'shipping:street1',
	    			id: 			'shipping:street1',
	    			type: 			'text',
	    			inputClassName: 'input-text required-entry form-control',
	    			labelClassName: 'required',
	    			labelText: 		'Address',
	    			placeholder: 	'Number and Street'
	    		},
	    		{
	    			name: 			'shipping:street2',
	    			id: 			'shipping:street2',
	    			type: 			'text',
	    			inputClassName: 'input-text form-control',
	    			labelClassName: 'required',
	    			labelText: 		'Address Line 2',
	    			placeholder: 	'Apartment Number',
	    			optional: 		true
	    		},
	    		{
	    			name: 			'shipping[city]',
	    			id: 			'shipping:city',
	    			type: 			'text',
	    			inputClassName: 'input-text required-entry form-control',
	    			labelClassName: 'required',
	    			labelText: 		'City',
	    			placeholder: 	'City'
	    		},
	    		{
	    			name: 			'shipping[region_id]',
	    			id: 			'shipping:region_id',
	    			type: 			'select',
	    			inputClassName: 'input-text required-entry form-control',
	    			labelClassName: 'required',
	    			labelText: 		'State/Province',
	    			placeholder: 	'Please select region, state or province',
	    			options: [
	    				{
	    					value: '43',
	    					label: 'New York'
	    				}
	    			],
	    			selected: 		''
	    		},
	    		{
	    			name: 			'shipping[postcode]',
	    			id: 			'shipping:postcode',
	    			type: 			'text',
	    			inputClassName: 'input-text required-entry form-control',
	    			labelClassName: 'required',
	    			labelText: 		'Postal Code',
	    			placeholder: 	'Postal Code'
	    		},
	    		{
	    			name: 			'shipping[country_id]',
	    			id: 			'shipping:country_id',
	    			type: 			'select',
	    			inputClassName: 'input-text required-entry form-control',
	    			labelClassName: 'required',
	    			labelText: 		'Country',
	    			placeholder: 	'Please select your country',
	    			options: [
	    				{
	    					value: 'US',
	    					label: 'United States'
	    				}
	    			],
	    			selected: 		''
	    		},
	    		{
	    			name: 			'shipping[telephone]',
	    			id: 			'shipping:telephone',
	    			type: 			'text',
	    			inputClassName: 'input-text form-control',
	    			labelClassName: 'required',
	    			labelText: 		'Phone Number',
	    			placeholder: 	'Phone Number',
	    			optional: 		true
	    		}
	    	],
	    	showNewForm: (!!props.addresses.all && props.addresses.all.length <= 0) || !props.loggedin ? true : false,
	    	formData: {},
	    	message: ''
	    };
	}
	updateChecked = (evt) => {
		var target = evt.target,
			val = target.value;

		var current_saved = this.state.current_saved,
			saved_addr = this.state.saved_addr;

		current_saved = saved_addr.options.find((addr) => {
			return Number(val) === addr.value;
		});

		saved_addr.selected = val;

		this.setState({
			current_saved: current_saved,
			continue_disabled: !!current_saved ? null : true,
			saved_addr: saved_addr
		});
	}
	toggleForm = (evt, target) => {
		let $target =  target || evt.target;
		let value = $target.value;

		let saved_addr = this.state.saved_addr;
		saved_addr.selected = value;

		let addr = saved_addr.options.find( (addr, index) => {
			return addr.value === value;
		});

		saved_addr.data_id = !!addr.data_id ? addr.data_id : null;

		console.log(saved_addr.data_id);

		this.setState({
			showNewForm: ~~value === -1,
			saved_addr: saved_addr
		});
	}
	_updateFormData = (evt, target) => {
		let $target = target || evt.target;
		let name = $target.getAttribute('name');
		let value = $target.value;

		let state = this.state;
		state.formData[name] = $target.value;

		if($target.nodeName.toLowerCase() === 'select'){
			state.fields.forEach((field) => {
				if(field.name === name){
					field.selected = value;
				}
			});
		}

		this.setState(state);
	}
	_checkFormStatus = () => {
		let formWrapper = ReactDOM.findDOMNode(this.refs.shippingFormWrapper);
		let form = formWrapper.querySelector('#shippingForm');
		let errorFound = form.querySelector('.has-error');
		if(!!errorFound){
			this.setState({
				continue_disabled: true
			});
			return false;
		}
		else {
			this.setState({
				continue_disabled: null
			});
			return true;
		}
	}
	clickedContinue = (evt) => {
		evt.preventDefault();
		if(this._checkFormStatus()){
			let nextStep = 4;
			
			if( this.props.workerUrl.indexOf('localhost:3000') <= -1 ){
				let formData = {};

				if(!!this.props.loggedin && !this.state.showNewForm && !!this.state.saved_addr){
					formData['shipping_address_id'] = this.state.saved_addr.selected;
					formData['shipping[address_id]'] = this.state.saved_addr.data_id;
				}
				else {
					formData = Object.assign({}, this.state.formData);
					formData['shipping[street][]'] = [formData['shipping:street1'], formData['shipping:street2']].join("\n");
				
					delete formData['shipping:street1'];
					delete formData['shipping:street2'];
				}

				let _errorCheck = (msg) => {
					let json = JSON.parse(msg);
					if(json.error){
						return this.setState({
							message: json.message
						});
					}

					let shipping_methods = this.props.shipping_methods;
					if(!!json.update_section && !!json.update_section.methods){
						shipping_methods.all = json.update_section.methods.map( (method) => {
							return Object.assign({}, shipping_methods.defaultStyles, method);
						});
					}

					this.props.onClickHandler({
						shipping_methods: shipping_methods,
						currentStep: nextStep
					});
				};

				let myWorker = new QueryableWorker(this.props.workerUrl, _errorCheck, _errorCheck);
				myWorker.sendQuery('set', {url: this.props.save.shipping, body: formData});
			}

			if( this.props.workerUrl.indexOf('localhost:3000') > -1 ){
				this.props.onClickHandler({
					currentStep: nextStep
				});
			}
		}
	}
	componentWillReceiveProps(nextProps) {
		let showNewForm = (!!nextProps.addresses.all && nextProps.addresses.all.length <= 0) || !nextProps.loggedin ? true : false;
	    let current_saved = !!nextProps.addresses.shipping ? nextProps.addresses.shipping : null;

	     this.setState({
	     	current_saved: current_saved,
	     	continue_disabled: !!current_saved ? null : true,
	     	showNewForm: showNewForm
	     });
	}
	render() {
		let savedAddrHTML = null;
		let newAddrHTML = null;
		let msgHTML = null;

		if(!!this.state.message){
			msgHTML = <section className="col-xs-12" ref="messageWrapper"><p>{this.state.message}</p></section>;
		}

		let savedAddrClassName = "col-xs-12 shipping-saved";
		let newAddrClassName = "col-xs-12 shipping-form-wrap";

		if(!this.props.loggedin || this.props.addresses.all.length <= 0){
			savedAddrClassName += ' hidden';
		}

		if(!this.state.showNewForm){
			newAddrClassName += ' hidden';
		}

		let newAddrFields = this.state.fields;
		let saved_addr = this.state.saved_addr;

		savedAddrHTML = !this.props.loggedin ? '' : <section className={savedAddrClassName}>
			<SelectBox {...saved_addr} onUpdateValue={this.toggleForm} />
			<div className="button-row">
				<button type="button" className="btn btn-primary pull-right" onClick={this.clickedContinue} ref="continueBtn1">Continue</button>
			</div>
		</section>;
		
		newAddrHTML = <section className={newAddrClassName} ref="shippingFormWrapper">
			<h4>Add a New Address</h4>
			<FormFields fields={newAddrFields} formId="shippingForm" onInputChange={this._updateFormData} onUpdateValue={this._updateFormData} />
			<div className="button-row">
				<button type="button" className="btn btn-primary pull-right" onClick={this.clickedContinue} ref="continueBtn2">Save &amp; Continue</button>
			</div>
		</section>;

		return (<article className="step-wrapper shipping-info">
			{msgHTML}
			{savedAddrHTML}
			{newAddrHTML}
		</article>);
	}
}

Shipping.defaultProps = {
	onClickHandler: (evt) => {
    	evt.preventDefault();
    	return console.log(evt);
    }
}



